<template>
  <div>
    <BasePanel>
      <template #panel-head>
        <router-link to="/">主页</router-link>
        /
        <span>新消息</span>
      </template>
      <template #panel-content>
        <!-- 未读消息展示 -->
      </template>
    </BasePanel>
    <BasePanel>
      <template #panel-head>
        <span>过往信息</span>
      </template>
      <template #panel-content>
        
      </template>
    </BasePanel>
  </div>
</template>

<script>
import BasePanel from "../components/layout/BasePanel.vue";
import {markAllMessage, getMessage} from '../plugins/api'
export default {
  data(){
    return{
      messages: null
    }
  },
  components: { BasePanel },
  async created(){
    const res = await getMessage(sessionStorage.getItem('accesstoken'))
    this.messages = res.data
    // 有未读消息的话 需要标记为已读
    if(res.data.hasnot_read_messages.length){
      markAllMessage(sessionStorage.getItem('accesstoken'))
    }
  }
};
</script>

